* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
body {
  background-color: violet;
}
.container {
  background-color: white;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  border-radius: 10px;
  box-shadow: 2px, 3px rgb(146, 141, 141);
  width: 600px;
  padding: 80px 50px;
}
.search-box {
  display: flex;
  justify-content: space-between;
  flex-flow: row;
}
#search-engine {
  width: 70%;
  outline: none;
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  border: none;
  margin-bottom: 7px;
  border-bottom: 2px solid violet;
}
#search-button {
  border: none;
  outline: 1px solid white;
  background-color: violet;
  border-radius: 6px;
  padding: 6px;
  color: white;
}
#word-sound {
  position: relative;
}
.word {
  display: flex;
  justify-content: space-between;
  flex-flow: row;
  margin-top: 40px;
}
#volume {
  border: none;
  font-size: 18px;
  outline: none;
  color: violet;
}
.details {
  display: flex;
  margin: 15px 10px 10px 0px;
  gap: 10;
  color: grey;
}
#word-meaning {
  color: rgb(192, 121, 121);
  font-size: 20px;
  font-family: Poppins;
}
#word-example {
  color: grey;
  margin: 15px 0px 10px 0px;
  border-left: 5px solid violet;
  padding-left: 7px;
  font-size: 18px;
  font-style: italic;
}
